.product-img__main {
  position: relative;
}

ul.product-img__main-slides {
  width: 100%;
  height: 28.125rem;
  position: relative;
}
ul.product-img__main-slides li {
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
ul.product-img__main-slides li.active {
  opacity: 1;
  visibility: visible;
}
ul.product-img__main-slides li a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
ul.product-img__main-slides li a img {
  width: auto;
  height: 100%;
}
@media (min-width: 71.875em) {
  ul.product-img__main-slides li a {
    border-radius: 0.9375rem;
  }
}

.product-img__main-nav {
  position: absolute;
  top: 45%;
  width: 100%;
}
@media (min-width: 71.875em) {
  .product-img__main-nav {
    display: none;
  }
}
.product-img__main-nav .nav-prev,
.product-img__main-nav .nav-next {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border: 1px solid white;
  background: hsl(0deg, 0%, 100%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 0.75rem;
  margin: 0px 1rem;
  border-radius: 100%;
  color: unset;
  position: absolute;
  cursor: pointer;
  opacity: 0.8;
}
.product-img__main-nav .nav-prev:hover, .product-img__main-nav .nav-prev:focus,
.product-img__main-nav .nav-next:hover,
.product-img__main-nav .nav-next:focus {
  opacity: 1;
}
.product-img__main-nav .nav-prev {
  left: 0;
  background-image: url("../images/icon-previous.svg");
}
.product-img__main-nav .nav-next {
  right: 0;
  background-image: url("../images/icon-next.svg");
}

ul.product-img__main-thumbs {
  padding: 0.625rem 0.3125rem;
  display: none;
}
@media (min-width: 71.875em) {
  ul.product-img__main-thumbs {
    display: flex;
  }
}
ul.product-img__main-thumbs li {
  width: 25%;
  padding: 0 0.3125rem;
}
ul.product-img__main-thumbs li.selected a {
  border-color: hsl(26deg, 100%, 55%);
}
ul.product-img__main-thumbs li.selected a::after {
  content: "";
  background: hsl(0deg, 0%, 100%);
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.6;
}
ul.product-img__main-thumbs li a {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 0.9375rem;
  overflow: hidden;
  border: 0.125rem solid transparent;
}
ul.product-img__main-thumbs li a img {
  width: 100%;
  height: 100%;
}
ul.product-img__main-thumbs li a::after {
  content: "";
  background: hsl(0deg, 0%, 100%);
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
}
ul.product-img__main-thumbs li a:hover {
  border-color: hsl(26deg, 100%, 55%);
}
ul.product-img__main-thumbs li a:hover::after {
  opacity: 0.6;
}